.kit__title {
  	@include title-light();
}
.kit {
  background-color: #0f200c;
  @include block-padding();
  background-image: url(../img/kit-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;

      background-repeat: no-repeat;
    background-position: 0px;
    background-size: cover;
    border-top: 1px solid transparent;
    border-image: url(../img/top-border-3.png) round;
    border-image-width: 60px 0 0 0;
    border-image-slice: 100;
    border-image-outset: 0px 0px 0px 0px;
}
.kit__cards {
  display: flex;
  justify-content: space-between;
  vertical-align: middle;
  flex-wrap: wrap;
}
.kit__cards-left {
  width: 470px;
  @media (max-width: 1024px) {
    width: 100%;
  }

}
.kit__cards-right {
  width: 670px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  vertical-align: middle;

  @media (max-width: $desktop-width) {
    width: 100%;
  }
  @media (max-width: $tablet-width) {
    display: block;
  }
}
.kit__card {
  background-color: rgba(21, 55, 38, 0.7);
  border-radius: 5px;
  padding: 40px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);

  @media (max-width: $tablet-width) {
    padding: 25px;
  }
}
.kit__card--1 {
  width: 470px;
  min-height: 600px;
  background-image: url("../img/kit-img-1.png");
  background-repeat: no-repeat;
  background-position: right bottom;

  @media (max-width: 1024px) {
    width: 100%;
    margin-bottom: 30px;
  }

  @media (max-width: $tablet-width) {
    min-height: 250px;
    background-size: contain;
  }

}
.kit__card--2 {
  width: 370px;
  min-height: 374px;
  background-image: url("../img/kit-img-2.png");
  background-repeat: no-repeat;
  background-position: left bottom;

  @media (max-width: $desktop-width) {
    width: 50%;
  }

    @media (max-width: $tablet-width) {
      width: 100%;
      min-height: 250px;
      margin-bottom: 30px;
      background-size: contain;
    }
}
.kit__card--3 {
  width: 270px;
  min-height: 374px;
  background-image: url("../img/kit-img-3.png");
  background-repeat: no-repeat;
  background-position: left bottom;

  @media (max-width: $desktop-width) {
    width: 45%;
  }
  @media (max-width: $tablet-width) {
    width: 100%;
    min-height: 250px;
    background-size: contain;
    background-position: right bottom;
  }
}
.kit__card--4 {
  width: 100%;
  min-height: 196px;
  margin-top: 29px;
    background-image: url("../img/kit-img-4.png");
  background-repeat: no-repeat;
  background-position: left bottom;
  background-size: cover;
  @media (max-width: $tablet-width) {
    width: 100%;
    min-height: 250px;
    background-size: 110%;
    background-position: left center;
  }
}
.kit__card--5 {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 470px;
  min-height: 316px;
  margin-top: 29px;
  background: none;
  box-shadow: none;

  color: #ffffff;

  @media (max-width: 1024px) {
    margin-right: auto;
    margin-left: auto;
  }
}
.kit__card-subtitle {
  font-size: 16px;
  margin: 0;
  color: #e0e0e0;
  @media (max-width: $desktop-width) {
    font-size: 14px;
  }
}

.kit__card--5 > .kit__descr {
  font-size: 18px;
  margin: 0;
  line-height: 24px;
  margin-bottom: 10px;
  text-align: center;
}

.kit__card--5 > .kit__descr > .kit__card-text-style {
  font-size: 22px;
  font-weight: bold;
}
.kit__card--5 > .kit__descr--timer {
  margin-bottom: 10px;
}
.kit__card--6 {
  width: 670px;
  min-height: 316px;
  margin-top: 29px;
    background-image: url("../img/kit-img-6.png");
  background-repeat: no-repeat;
  background-position: right top;

  @media (max-width: $desktop-width) {
    width: 100%;
  }
  @media (max-width: $tablet-width) {
    width: 150%;
    min-height: 250px;
    background-size: contain;
    background-position: right top;
  }
}
.kit__card-title {
  margin: 0;
  color: white;
  font-size: 26px;
  font-weight: bold;
  font-family: Bebas Neue, PT Sans;

  @media (max-width: $desktop-width) {
    font-size: 19px;
  }
}
.kit__img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.kit__card-btn {
  padding: 7px 26px;
  font-size: 13px;
}
.kit__card-time {
  position: absolute;
  margin: 0;
  font-size: 13px;
}
.kit__card-time > span {
  font-weight: bold;
  font-size: 16px;
}
.kit__card-time--day {
  left: 0;
  top: 0;
}
.kit__card-time--hours {
  right: 0;
  top: 0;
}
.kit__card-time--minuts {
  left: 0;
  bottom: 0;
}
.kit__card-time--seconds {
  right: 0;
  bottom: 0;
}
.kit__descr-time-left {
  font-size: 13px;
  margin: 0;
  margin-bottom: 16px;
}
