@mixin container() {
	width: 1170px;
	margin: 0 auto;
	/*padding-left: 30px;
	padding-right: 30px;*/

	@media (max-width: $desktop-width) {
			width: 768px;
			/*padding-left: 30px;
			padding-right: 30px;*/
		}

	@media (max-width: $tablet-width) {
	
			width: 290px;
			margin: 0 auto;
			padding-left: 20px;
			padding-right: 20px;
	}
}
@mixin sub-title() {
	@media (max-width: $desktop-width) {
		margin: 0;
		font-size: 18px;
		margin: 0;
		margin-bottom: 7px;
		font-weight: bold;
	}
}
@mixin title-black() {
	display: inline-block;
	margin: 0;
	margin-bottom: 75px;
	z-index: 2;
	position: relative;
	font-size: 61px;
	line-height: 58px;
	font-weight: bold;
	color: #000000;

	font-family: "Bebas Neue", "Arial", sans-serif;
	@media (max-width: $desktop-width) {
		font-size: 27px;
		margin-bottom: 45px;
	}
	@media (max-width: $tablet-width) {
		display: inline-block;
		font-size: 22px;
		line-height: 25px;
	}
	&::after {
		position: absolute;
		top: 23px;
		left: -3%;
		content: "";
		height: 9px;
		width: 106%;
		background-color: #c2e0ba;
		z-index: -1;

		@media (max-width: $tablet-width) {
			display: none;
			top: 42px;
	    left: -3%;
	    content: "";
	    height: 6px;
	    width: 106%;
	    background-color: #54654f;
	    z-index: -1;
		}


	}

}
@mixin title-light() {
	display: inline-block;
	margin: 0;
	margin-bottom: 75px;
	z-index: 2;
	position: relative;
	font-size: 61px;
	line-height: 58px;
	font-weight: bold;
	color: #ffffff;

	font-family: "Bebas Neue", "Arial", sans-serif;

	&::after {
		position: absolute;
		top: 23px;
		left: -3%;
		content: "";
		height: 9px;
		width: 106%;
		background-color: #54654f;
		z-index: -1;

		@media (max-width: $tablet-width) {
			display: none;
			top: 42px;
	    left: -3%;
	    content: "";
	    height: 6px;
	    width: 106%;
	    background-color: #54654f;
	    z-index: -1;
		}
	}


	@media (max-width: $desktop-width) {
		font-size: 27px;
		margin-bottom: 45px;
	}
	@media (max-width: $tablet-width) {
		display: inline-block;
		font-size: 22px;
		line-height: 25px;
	}
}

@mixin block-padding() {
	padding: 150px 0px;

	@media (max-width: $desktop-width) {
		padding: 40px 0px;
	}
}
