.question {
@include block-padding();
  padding: 150px 0px;
    background-color: #0f200c;
    color: #ffffff;
    background-image: url(../img/questions-background.png);
    background-repeat: no-repeat;
    background-position: 0px;
    background-size: cover;
    border-top: 62px solid transparent;
    border-image: url(../img/top-border-3.png) round;
    border-image-width: 60px 0 0 0;
    border-image-slice: 100;
    border-image-outset: 0px 0px 0px 0px;
  
  @media (max-width: $desktop-width)  {
    padding-bottom: 130px;
  }
  
  @media (max-width: $tablet-width) {
    border-top: 1px solid transparent;
    border-image: url(../img/top-border-4.png) round;
    border-image-width: 18px 0 0 0;
    border-image-slice: 100;
    border-image-outset: -18px 0px 0px 0px;
  }
}
.question__title {
  @include title-light()
  margin-bottom: 80px;

  
}

/* Style the buttons that are used to open and close the accordion panel */
.accordion-wrapper {
  max-width: 66%;
  display:block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 90px;

  @media (max-width: $desktop-width) {
    max-width: none;
    margin-bottom: 40px;
  }
}

.accordion {
    position: relative;

    // margin-bottom: 15px;
    min-height: 70px;
    background-color: #153726;
    color: #ffffff;
    cursor: pointer;
    padding: 18px;
    padding-left: 42px;
    padding-right: 30px;
    width: 100%;
    text-align: left;
    border: none;
    // border-radius: 10px;
    outline: none;
    transition: 0.4s;

    // mask: url(../img/mask-block.svg) top left / cover;
    // -webkit-mask: url(../img/mask-block.svg) top left / cover;

    -webkit-mask-box-image: url(../img/mask-block.svg) 30 repeat;
    mask-border: url(../img/mask-block.svg) 30 repeat;

    mask-border-repeat: stretch; //края маски растягиваются
    // mask-border-repeat: repeat; //края маски повторяются
    // mask-border-repeat: round; //края маски растягиваются, могут быть обрезаны

    mask-border-outset: 0px 0px 0px 0px;   //отступы края маски относительно контейнера

    @media (max-width: $desktop-width) {
      padding-left: 24px;
      padding-right: 42px;
    }

}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active, .accordion:hover {
    background-color: #1c5337;

}

/* Style the accordion panel. Note: hidden by default */
.panel {
  max-width: 100%;
  // min-height: 70px;

  padding: 0 18px;

  padding-left: 42px;
  padding-right: 42px;


  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;

  margin-bottom: 10px;
  margin-top: 5px;
}

/* Иконки для аккордеона */
.accordion:after {
    // content: '\02795'; /* Unicode character for "plus" sign (+) */
    content: "";
    position: absolute;
    right: 30px;
    top: 26px;
    width: 22px;
    height: 22px;
    background-image: url("../img/acordeon-icon.png");
    background-size: cover;
    background-repeat: no-repeat;

    font-size: 13px;
    color: #777;
    float: right;
    margin-left: 5px;

    @media (max-width: $desktop-width) {
      width: 16px;
      height: 16px;
      right: 14px;

    }
}

.active:after {
    // content: "\2796"; /* Unicode character for "minus" sign (-) */
    transform: rotate(180deg);
}
.question__btn {
  padding: 15px 30px;

  @media (max-width: $desktop-width) {
    padding: 9px 18px;
    font-style: 13px;
  }
}
