.delivery {
  background-color: #0f200c;
  color: #ffffff;
  @include block-padding();

  border-top: 62px solid transparent;
  border-image: url("../img/top-border-3.png") round;
  border-image-width: 60px 0 0 0;
  border-image-slice: 100;
  border-image-outset: 0px 0px 0px 0px;
  
  @media (max-width: $desktop-width) {
    padding-bottom: 130px;
  }

  @media (max-width: $tablet-width) {
    border-top: 1px solid transparent;
    border-image: url("../img/top-border-3.png") round;
    border-image-width: 18px 0 0 0;
    border-image-slice: 100;
    border-image-outset: -18px 0px 0px 0px;
  }
}
.delivery__title {
  @include title-light();
  @media (max-width: $desktop-width) {
    margin-bottom: 0;
  }
}
.delivery__map-wrapper {
  position: relative;

  min-height: 415px;
    width: 770px;
    

    @media (max-width: $desktop-width) {
      display: flex;
      flex-direction: column;
      width: 100%;
      min-height: auto;
    }
}
.delivery__content-wrapper {
  display: flex;
  justify-content: space-between;

  @media (max-width: $desktop-width) {
    display: flex;
    flex-direction: column-reverse;
  }
}
.delivery__text-block {
  width: 370px;
  @media (max-width: $desktop-width) {
    width: auto;
    margin-bottom: 40px;
  }
}
.delivery__ya-map {
  position: relative;
  z-index: 1;
}
.delivery__map {
  top: 0;
  z-index: 0;
  position: absolute;
}
