.interesting {
  position: relative;
  @include block-padding();
  background-color: #ffffff;

  // border-bottom: 60px solid transparent;
  // border-image: url("../img/top-border-1.png") round;
  // border-image-width: 60px;
  // border-image-slice: 100;
  // border-image-outset: 0px 0px 60px 0px;

  border-top: 1px solid transparent;
  border-image: url("../img/top-border-1.png") round;
  border-image-width: 60px;
  border-image-slice: 190;
  border-image-outset: 55px 0px 0px 0px;

  z-index: 2;

  @media (max-width: $desktop-width) {
    padding-bottom: 33px;
    border-image-width: 55px;
    border-image-outset: 55px 0px 0px 0px;
  }
  @media (max-width: $tablet-width) {
     border-image-width: 27px;
    border-image-outset: 27px 0px 0px 0px;
  }
 
}
.interesting::after {
  position: absolute;
  content: url("../img/branch.png");
  top: 30px;
  right: 0;
  @media (max-width: $desktop-width) {
    position: absolute;
    content: url(../img/branch-tablet.png);
    top: -26px;
      /* right: -6px; */
    max-width: auto;
  }
  @media (max-width: $tablet-width) {
    position: absolute;
    content: url(../img/branch-mobile.png);
    top: -23px;
      /* right: -6px; */
    max-width: auto;
  }

}
