.interesting__title {
  @include title-black();
  margin: 0;
  margin-bottom: 65px;
  @media (max-width: $desktop-width) {
    // font-size: 18px;
    margin: 0;
  }
}
.interesting__descr {
  max-width: 625px;
  margin: 0;
  padding: 0;
  margin-bottom: 65px;
  @media (max-width: $desktop-width) {
    margin-bottom: 40px;
    max-width: 468px;
  }
}
.interesting__cards-wrapper {
  display: flex;
  @media (max-width: $desktop-width) {
    display: block;
  }

}

.interesting__card-column {
  display: flex;
  flex-direction: column;
    width: 390px;
    @media (max-width: $desktop-width) {
      width: auto;
      margin-bottom: 45px;
    }
}
.interesting__card,
.interesting__card-img {
  min-height: 400px;

}
.interesting__card-img {
  @media (max-width: $desktop-width) {
    display: none;
  }
}
.interesting__card {
  padding: 45px;
  align-items: center;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  @media (max-width: $desktop-width) {
    padding-top: 0;


    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    min-height: auto;

  }
}
.interesting__card-icon {
  max-width: 50px;
  margin-bottom: 15px;
}
.interesting__card-img--1 {
  background-image: url(../img/interesting-img-1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.interesting__card-img--2 {
  background-image: url(../img/interesting-img-2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.interesting__card-img--3 {
  background-image: url(../img/interesting-img-3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.interesting__card-descr {
    max-width: none;
    margin: 0;
  @media (max-width: $desktop-width) {
    max-width: 337px;
  }
  @media (max-width: $mobile-width-only) {
    max-width: none;
  }
}
.interesting__card-title {
  margin: 0;
  margin-bottom: 11px;
}
