	// Анимация туман
.fog-animation__wrapper {
	position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 945px;
  overflow: hidden;
  // background: url("background.jpg") center center;
  background-size: contain;
  z-index: -2;
}
.fog-animation__wrapper::before,
.fog-animation__wrapper::after,
.fog-animation__wrapper div::before,
.fog-animation__wrapper div::after {
    content: "";
    position: absolute;
    top: 50px;
    left: 0;
    height: 100%;
    width: 100%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}
.fog-animation__wrapper div::before,
.fog-animation__wrapper div::after    {
    left: 100%;
}
.fog-animation__wrapper::before,
.fog-animation__wrapper div::before {
    background-image: url("../img/fog1.png");
    background-size: contain;
		background-repeat: repeat-x;
    animation: fogmove 17s linear 0s infinite;
    background-position-y: bottom;
}
.fog-animation__wrapper::after,
.fog-animation__wrapper div::after {
    background-image: url("../img/fog2.png");
    animation: fogmove 65s linear 0s infinite;
		background-repeat: repeat-x;
}
@keyframes fogmove {
    from {
        transform: translate3d(0, 0, 0);
    }
    to {
        transform: translate3d(-100%, 0, 0);
    }
}
