.contacts {
  @include block-padding();
  background-color: #ffffff;

  padding: 150px 0px;
  background-color: #ffffff;
  border-top: 1px solid transparent;
  border-image: url(../img/top-border-2.png) round;
  border-image-width: 44px;
  border-image-slice: 100;
  border-image-outset: 44px 0px 0px 0px;
  background-color: #ffffff;

  @media (max-width: $tablet-width) {
    background-color: #ffffff;
    border-top: 1px solid transparent;
    border-image: url(../img/top-border-2.png) round;
    border-image-width: 18px;
    border-image-slice: 100;
    border-image-outset: 18px 0px 0px 0px;
    background-color: #ffffff;
  }
}
.contacts__title {
  @include title-black();
}
.contacts__form {
  display: flex;
  justify-content: space-between;
  @media (max-width: $desktop-width) {
    display: block;
  }
}
.form__info-block {
  width: 440px;
  padding: 60px;
  padding-right: 0;
  background-color: #0f200c;
  color: #ffffff;
  background-image: url("../img/form-img-bg.png");
  background-repeat: no-repeat;
  background-size: cover;

  @media (max-width: $desktop-width) {
    display: block;
    width: auto;
    padding: 30px;
    width: 475px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 69px;
    margin-bottom: 72px;
  }
  @media (max-width: $tablet-width) {
    width: auto;
  }
}
.form__fields {
  width: 730px;
  padding: 60px;

  @media (max-width: $desktop-width) {
    width: auto;
    padding: 0px;
    width: 415px;
    margin-left: auto;
    margin-right: auto;

  }
  @media (max-width: $tablet-width) {
    width: auto;
  }
}
.fields__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: $desktop-width) {
    display: block;
    margin-bottom: 40px;
  }
}
.fields__item {
   width: 265px;
   @media (max-width: $desktop-width) {
     width: auto;
     margin-bottom: 26px;
   }
}
// .fields__item:nth-child(2n+1) {
//   margin-right: 60px;
// }
.form__btn {
  margin-left: auto;
  @media (max-width: $desktop-width) {
    margin-right: auto;
  }
}
.fields__name {
  width: 100%;
  min-height: 37px;
}
.fields__name::placeholder {
  color: #acafb1;
  font-family: "Pt Sans", "Arial", sans-serif;
}

.fields__one-block {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 60px;

  @media (max-width: $desktop-width) {
    display: block;
    margin-bottom: 0;
  }
}
.fields__two-block {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 35px;
  margin-left: auto;

  @media (max-width: $desktop-width) {
    display: block;
    margin-bottom: 0;
  }
}
.form__btn {
  display: block;
  margin-left: auto;
}
.fields__title {
  font-size: 18px;
  font-weight: bold;
}
.field-name {
  font-size: 18px;
  font-weight: bold;
}
.fields__name {
  border: none;
  border-bottom: 1px solid #dcddde;
}
.fields__name--textarea {
  margin-top: 10px;
  border-top: 0;
  border: 1px solid #dcddde;

  &::placeholder {
        font-family: "PT Sans", "Arial", sans-serif;
        line-height: 20px;
        margin-left: 10px;

  }
}
