.main-header  {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 225px;
  padding: 75px 0px;
  // margin-bottom: 150px;
  // background-color: #0f200c;
  color: #ffffff;

  @media (max-width: $mobile-width-only) {
    display: block;
    padding: 10px 0px;
    margin-bottom: 50px;
    min-height: auto;
  }
  @media (max-width: $desktop-width) {
    padding: 32px 0px;
    min-height: auto;
  }
  @media (max-width: $tablet-width) {
    margin-bottom: 0;
  }
}
.main-nav__btn {
  display: none;
  @media (max-width: $mobile-width-only) {
    display: block;
    width: 20px;
    height: 20px;

    position: absolute;
    top: 35px;
    right: 0;
    background: none;
    border: none;
    z-index: 999;
  }
  @media (max-width: $tablet-width) {
    right: 13px;
  }
}
.main-nav__btn::after {
  position: absolute;
  content: "";
  position: absolute;
  top: 1px;
  left: -2px;
  height: 2px;
  width: 20px;
  background-color: #ffffff;
  box-shadow: 0 6px 0 0 #ffffff, 0 12px 0 0 #ffffff;
}
.main-nav--opened {
  display: block;
}
.main-nav--opened .main-nav__btn::after,
.main-nav--opened .main-nav__btn::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: none;
}
.main-nav--opened .main-nav__btn::after {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__btn::after {
  transform: rotate(-45deg);
}


.phone__item {
  margin: 0;
  margin-bottom: 6px;
  @media (max-width: $mobile-width-only) {
    margin: 0;
  }
}
.container--header {
  position: relative;
  @media (max-width: $mobile-width-only) {
  position: static;
  }
}
.main-header__logo {
  max-width: 36px;

  margin-right: 130px;
  @media (max-width: $mobile-width-only) {
  margin-right: 20px;
  max-width: 40px;
  min-height: 40px;
  }
}
.main-header__phone {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-right: auto;
  @media (max-width: $mobile-width-only) {
    // margin-bottom: 25px;
  }
}
.main-nav {
  position: absolute;
  right: 0;
  top: 0px;
  z-index: 10;

  @media (max-width: $mobile-width-only) {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(15, 32, 12, 0.8);

    display: none;
    padding: 40px;
    z-index: 998;
  }
}
.main-nav--opened {
  display: block;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__list-item {
  position: relative;
  margin-bottom: 20px;

  &::after {
    opacity: 0;
  }

  &:hover::after {
    position: absolute;
    content: "";
    opacity: 1;
    width: 72px;
    height: 2px;
    background-color: #f47736;
    top: 14px;
    left: -88px;
    transition: all 0.2 ease-in-out;
  }
}
.main-nav__link {
  position: relative;
  text-decoration: none;
  color: #ffffff;
}
 .main-nav__link:active::before {
  opacity: 0;
  content: "";
  width: 20px;
  height: 2px;
   background-color: orange;
   top: 0px;
    right: 60px;
 }
/*.main-nav__link:hover {
  font-size: 17px;
  transition: all 0.5s ease;
}*/

.main-header__top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: $mobile-width-only) {
    margin-bottom: 35px;
  }
  @media (max-width: $tablet-width) {
    margin-bottom: 0px;
  }
}
.phone__link {
    color: #ffffff;
    font-size: 12px;
    line-height: 12px;
    /* background-color: #fa8548; */
    background-color: transparent;
    border: 1px solid #fa8548;
    border-radius: 4px;
    display: block;
    padding: 4px 5px;
    max-width: 111px;
    margin: 0;
    text-decoration: none;
    text-align: center;

  &:hover {
    text-decoration: none;
    border: 1px solid #ffffff;
    transition: all 0.5s ease;
  }
}
.logo__img {
  fill: #ffffff;
  @media (max-width: $tablet-width) {
    width: 60%;
  }
}
.header-callback {
  position: absolute;
  display: none;
  width: 170px;
  border-radius: 5px;
  background-color: #ffffff;
  padding: 21px 28px 21px 28px;
  top: 65px;
}
.header-callback--show {
  display: block;
}
.callback__field {
    border: none;
  border-bottom: 1px solid #dcddde;
  max-width: 112px;
  margin-bottom: 7px;
  line-height: 25px;
  width: 100%;
}
.callback-btn {
  font-size: 10px;
  padding: 7px 11px;
  display: block;
  margin-top: 10px;
}
#callback-error-block {
  color: black;
  font-size: 12px;
  padding: 5px 0px;
}