.descript {
  // background-color: #0f200c;
  min-height: 680px;
  @media (max-width: 1024px) {
      min-height: 561px;
  }
}
.descript__container {
  position: relative;
  z-index: 1;
   @media (max-width: 1024px) {
    padding-top: 64px;
  }

  @media (max-width: $tablet-width) {
    padding-top: 30px;
  }
}
.descript__bg-text {
  position: absolute;
  bottom: 23px;
  left: -15px;
  z-index: 3;

  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: bold;
}
.descript__bg-text-item {
  margin: 0;
  font-size: 298px;
  line-height: 240px;
  color: #ffffff;
  opacity: 0.15;
  text-transform: uppercase;
  @media (max-width: $desktop-width) {
  font-size: 170px;
  }
  @media (max-width: $tablet-width) {
  font-size: 73px;
  }
}

.descript-text {

  font-family: "Neucha", "PT Sans", sans-serif;
  max-width: 350px;
  font-size: 70px;
  line-height: 81px;
  margin: 0;
  margin-bottom: 55px;
  margin-top: 70px;
  color: #ffffff;

  @media (max-width: $desktop-width) {
    font-size: 40px;
    line-height: 48px;
    max-width: 274px;

    margin: 0;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  @media (max-width: 1024px) {
    font-size: 55px;
    line-height: 60px;
  }

 @media (max-width: $tablet-width) {
    font-size: 44px;
    line-height: 49px;
  }


}

.descript-btn {
  position: relative;
  z-index: 4;
  margin-bottom: 250px;
  @media (max-width: $desktop-width) {
      margin-bottom: 200px;
  }
  @media (max-width: $tablet-width) {
      font-size: 13px;
      margin-bottom: 165px;
  }
}
.descript__ant2 {
  position: absolute;
    right: -2.3vw;
    top: -33px;
    z-index: -2;

    animation: ant 1.5s infinite ease-in-out;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: ant;
}

@keyframes ant {
    0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
    50% {
    -webkit-transform: rotate(-3deg);
    transform: rotate(-3deg);
  }
}

