.inhabitants {
  @include block-padding();
  background-color: #0f200c;
  color: #ffffff;

  border-top: 62px solid transparent;
  border-image: url("../img/top-border-3.png") round;
  border-image-width: 60px 0 0 0;
  border-image-slice: 100;
  border-image-outset: 0px 0px 0px 0px;

  @media (max-width: $desktop-width) {
    padding-top: 50px;
    border-top: 25px solid transparent;
    border-image-width: 25px 0 0 0;
  }
}
.inhabitants__descr {
  margin: 0;
  margin-bottom: 50px;
}
.inhabitants__title {
  margin-bottom: 78px;
  @include title-light();
}
.inhabitants__card-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.inhabitants__card-title {
  display: inline-block;

  max-width: 70px;

  margin: 0;
  font-family: "Bebas Neue", sans-serif;
  background-color: #0f200c;
  padding: 10px 15px;

  border-top-left-radius: 3% 55%;
  border-top-right-radius: 50% 5%;
  border-bottom-right-radius: 13% 6%;
  border-bottom-left-radius: 4% 10%;
  @media (max-width: $desktop-width) {
    padding: 0px 10px;
    margin-bottom: 20px;
  }
}
.inhabitants__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 25px 40px;
  background-color: #153726;
  margin-bottom: 30px;
  min-height: 330px;

//   clip-path: url(#myPath);
// clip-path: polygon(100% 100%);
  // mask-image: url(#mask);
  // mask: url(../img/mask-block.svg) top left / cover;
  // -webkit-mask: url(../img/mask-block.svg) top left / cover;

  -webkit-mask-box-image: url(../img/mask-block.svg) 30 repeat;
  mask-border: url(../img/mask-block.svg) 30 repeat;

  mask-border-repeat: stretch; //края маски растягиваются
  // mask-border-repeat: repeat; //края маски повторяются
  // mask-border-repeat: round; //края маски растягиваются, могут быть обрезаны

  mask-border-outset: 0px 0px 0px 0px;   //отступы края маски относительно контейнера

mask-box-image: url(../img/mask-block.svg) 30 repeat;
  mask-border: url(../img/mask-block.svg) 30 repeat;

  mask-border-repeat: stretch; //края маски растягиваются

  @media (max-width: $desktop-width) {
    padding: 19px 19px;
  }
}
.inhabitants--xl {
  width: 670px;
  background-image: url("../img/ant-matka-3.3.jpg");
  @media (max-width: $desktop-width) {
    width: 100%;
    background-image: url("../img/ant-matka-mobile.jpg");
  }
}
.inhabitants--l {
  width: 470px;
  background-image: url("../img/ant-soldier.jpg");
  @media (max-width: $desktop-width) {
    
    background-image: url("../img/ant-soldier-mobile.jpg");
  }
}
.inhabitants--m {
  width: 470px;
  background-image: url("../img/ant-rab.jpg");
  min-height: 250px;
  @media (max-width: $desktop-width) {
    min-height: 328px;
    background-image: url("../img/ant-rab-mobile.jpg");
  }
}
.inhabitants--xs {
  width: 370px;
  min-height: 250px;
  background-image: url("../img/ant-kukolka.jpg");
   @media (max-width: $desktop-width) {
    
    background-image: url("../img/ant-kukolka-mobile.jpg");
  }
}

.inhabitants--s {
  width: 270px;
  min-height: 250px;
  background-image: url("../img/ant-lichinus.jpg");
  @media (max-width: $desktop-width) {
    
    background-image: url("../img/ant-lichinus-mobile.jpg");
  }
}
.panel {
  color: #000000;
}
.inhabitants__text {
  margin: 0;
  position: relative;
  padding-left: 12px;
  color: #000000;
  border-left: 3px solid #f47736;

  font-size: 14px;
  line-height: 18px;

  @media (max-width: $desktop-width) {
    padding: 0;
    font-size: 14px;
    line-height: 21px;
  }
}
