.scheme {
  @include block-padding();
  background-color: #ffffff;
  position: relative;
  
  
}
.scheme__title {
  @include title-black();
  margin-bottom: 140px;
}
.scheme__img {
  width: 90%;
  display: block;
  margin-left: auto;
  margin-right: auto;

  @media (max-width: $tablet-width) {
    width: 100%;
  }
}
.scheme__branch-1 {
  position: absolute;
  top: -99px;
  left: 0;
  opacity: 0.1;
  @media (max-width: $desktop-width) {
    max-width: 40%;
  }
  
}
.scheme__branch-2 {
  position: absolute;
  top: -10px;
  right: 0;
  opacity: 0.1;
  @media (max-width: $desktop-width) {
    max-width: 32%;
  }
}
