.feedback {
	@include block-padding();
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-color: #ffffff;
	width: 100%;

	border-top: 1px solid transparent;
    border-image: url(../img/top-border-1.png) round;
    border-image-width: 60px;
    border-image-slice: 190;
    border-image-outset: 55px 0px 0px 0px;

    background-image: url("../img/feedback-bg.jpg");
    background-repeat: no-repeat;
    background-position: top right;
}
.feedback__title {
	@include title-black();
}
.feedback__slider {
	padding-left: 190px;
	padding-right: 190px;

	@media (max-width: $tablet-width) {
		padding-left: 0px;
		padding-right: 0px;
	}
}
.feedback__text {
	position: relative;
	margin-bottom: 30px;
}
.feedback__card {
	position: relative;
	padding: 0 20px;
	min-height: 214px;
}
.feedback__card::after {
	position: absolute;
	left: 43.8%;
	top: -13px;
	margin-right: -95px;
	content: "";
	width: 95px;
	height: 82px;
	background-image: url(../img/quote.png);
	background-repeat: no-repeat;
	background-size: cover;
	z-index: -1;

	@media (max-width: $desktop-width) {
		left: 34.8%;
	}
}
.feedback__face {
	display: block;
	margin-left: auto;
	margin-right: auto;

	width: 60px;
	height: 60px;
	border-radius: 50%;
}
.feedback__slider .owl-item img {
	width: auto;
}
.feedback__name {
	display: block;
	margin-left: auto;
	margin-right: auto;

	font-size: 14px;
	font-weight: bold;
	text-transform: uppercase;

	margin-top: 5px;
}
.feedback__autor {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.feedback__slider .owl-nav button.owl-prev {
	right: -68px;
}