.video {
  @include block-padding();
  background-color: #ffffff;
  border-radius: 0px;


  border-top: 1px solid transparent;
  border-image: url("../img/top-border-2.png") round;
  border-image-width: 60px;
  border-image-slice: 100;
  border-image-outset: 60px 0px 0px 0px;
  background-color: #ffffff;

  @media (max-width: $tablet-width) {
    border-top: 1px solid transparent;
    border-image: url("../img/top-border-2.png") round;
    border-image-width: 18px;
    border-image-slice: 100;
    border-image-outset: 18px 0px 0px 0px;
    background-color: #ffffff;
  }
}
.video__title {
  @include title-black();
  margin-bottom: 55px;
}
.video__vid-wrapper {
  
  
  position: relative;
  padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
  padding-top: 30px;
  height: 0;
  overflow: hidden;

}
.video__vid-wrapper iframe {
  
 position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

}
.video__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  border-radius: 25px;


  background-color: #0f200c;
  opacity: 0.4;
}
.video video {
  border-radius: 0px;
}
.video__item {
  display: block;
  margin-left: auto;
  margin-right: auto;
}