.main-footer {

  padding: 80px 60px 80px 60px;
  min-height: 180px;
  background-color: #180e05;
  @media (max-width: $desktop-width) {
    padding: 55px 15px 55px 15px;
  }
}
.main-footer__list {
  display: flex;

  list-style: none;
  margin: 0;
  padding: 0;
  margin-right: 70px;

  @media (max-width: $desktop-width) {
    display: block;
    margin-right: 35px;
    margin-bottom: 15px;
    margin-right: 0;
  }
  @media (max-width: $tablet-width) {
    margin-bottom: 31px;
  }
}
.footer__list-item {

  margin-right: 30px;
  @media (max-width: $desktop-width) {
    margin-bottom: 14px;
  }
  // &:hover {
  //   transform: scale(1.1);
  // }
}
.footer__link {
  color: #ffffff;
  text-decoration: none;
}
.footer__link:hover {
   transform: scale(1.2);
   transition: all 0.5s ease;
}
.footer__content-wrapper {
  display: flex;
  justify-content: flex-start;
  // margin-bottom: 65px;
  @media (max-width: $desktop-width) {
    justify-content: space-between;
    margin-bottom: 0;
  }
  @media (max-width: $tablet-width) {
    flex-direction: column;
  }
}
.footer__created {
  margin: 0;
}
.social__img {
  width: 25px;
}
.social__link {
  margin-right: 16px;
  &:hover {
    transform: scale(1.1);
  }
}
.social__img--inst {
  width: 21px;
}
.footer__social-wrapper {
  display: flex;
  align-items: center;
  margin-right: 285px;

  @media (max-width: $desktop-width) {
    margin-bottom: 0;
    flex-direction: column;
    margin-right: 0;
  }
  @media (max-width: $tablet-width) {
    flex-direction: row;
    margin-bottom: 45px;
  }
}
.social__img--vk {
  width: 29px;
}
.footer__created {
  font-size: 13px;
  color: #b7b7b7;
}
.footer__created-link {
  color: #ffffff;
}
.inline-svg {
  display: none;
}
