.inhabitants-2 {
 @include block-padding();
  background-color: #FFFFFF;
  color: #000000;

      border-top: 1px solid transparent;
    border-image: url(../img/top-border-2.png) round;
    border-image-width: 60px;
    border-image-slice: 100;
    border-image-outset: 60px 0px 0px 0px;
    background-color: #ffffff;

  @media (max-width: $desktop-width) {
    padding-top: 50px;
    border-top: 25px solid transparent;
    border-image-width: 25px 0 0 0;
    border-image-outset: 20px 0px 0px 0px;
  }
}
.inhabitants-2__descr {

  @media (max-width: $tablet-width) {
    margin-bottom: 0px;
    width: auto;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
.inhabitants-2__slide {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @media (max-width: $desktop-width) {
    /*display: block;*/
  }
  @media (max-width: $tablet-width) {
    flex-direction: column-reverse;
    margin-bottom: 65px;
    align-items: center;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.inhabitants-2__slide-img {
  width: 60%;
  @media (max-width: $tablet-width) {
    width: 100%;
    // margin-bottom: 25px;
    width: 75%;
  }
}
.inhabitants-2__slide-text {
  width: 40%;

  @media (max-width: $desktop-width) {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  @media (max-width: $desktop-width) {
    margin-bottom: 30px;
  }
}
.inhabitants-2__control-wrapper {
  display: flex;
  max-width: 30%;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;

}
.inhabitants-2__control-item {
  width: 50%;
  border: 5px solid black;
  color: black;
  text-align: center;
  vertical-align: middle;
}
.control-item--left {
  border-right: none;
}
.inhabitants-2__slider-title {
  font-size: 45px;
  line-height: 52px;
  font-weight: bold;
  font-family: "Bebas Neue", "Arial", sans-serif;

  @media (max-width: $desktop-width) {
    font-size: 35px;
    margin: 0;

  }
  @media (max-width: $tablet-width) {
    font-size: 30px;
  }

}
.inhabitants-2__title {
  @include title-black();
  position: relative;
}
.inhabitants-2__title::before {
    content: url(../img/title-leafs.png);
    position: absolute;
    left: -68px;
    top: -54px;
    z-index: -1;
    opacity: 0.6;
}
.inhabitants-2__slide-text-content {
  margin: 0;
  max-width: 400px;
}
