.advantages {
  width: 100%;
  @include block-padding();
  padding-bottom: 210px;
  background-color: #0f200c;
  color: #ffffff;

  border-top: 62px solid transparent;
  border-image: url("../img/bottom-paper.png") round;
  border-image-width: 60px 0 0 0;
  border-image-slice: 100;
  border-image-outset: 0px 0px 0px 0px;

  @media (max-width: $desktop-width) {
      // padding: 50px 0px;
      padding-top: 20px;
      border-image-width: 25px 0 0 0;
  }
  @media (max-width: $tablet-width) {
    border-top: 62px solid transparent;
    border-image: url("../img/bottom-paper.png") round;
    border-image-width: 18px 0 0 0;
    border-image-slice: 100;
    border-image-outset: -18px 0px 0px 0px;
  }
}
.advantages__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 365px;
  padding-right: 80px;
  padding-left: 80px;

  background-image: url("../img/advantages-bg.jpg");
  background-repeat: repeat;
  background-size: cover;

  @media (max-width: $desktop-width) {
    display: block;
    max-width: 370px;
    margin-left: auto;
    margin-right: auto;
    min-height: auto;
    padding: 60px 35px 60px 35px;
    margin-bottom: 50px;


  }
}
.advantages__card {
    text-align: center;
    width: 230px;


  @media (max-width: $desktop-width) {
    width: 230px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 55px;
  &:last-child {
    margin-bottom: 0;
  }
  }

  @media (max-width: $mobile-width-only) {
    width: auto;
    margin-bottom: 55px;

  }

}
.advantages__title {
  @include title-light();
  @media (max-width: $tablet-width) {
    margin-bottom: 42px;
  }


}
.advantages__card-title {

}
.advantages__card-text {
  margin: 0;

}
