.catalog {
  @include block-padding();
  padding-bottom: 0;


  border-top: 1px solid transparent;
  border-image: url("../img/top-border-2.png") round;
  border-image-width: 60px;
  border-image-slice: 100;
  border-image-outset: 60px 0px 0px 0px;
  background-color: #ffffff;

  @media (max-width: $desktop-width) {
    border-top: 20px solid transparent;
    border-image: url("../img/top-border-2.png") round;
    border-image-width: 20px;
    border-image-slice: 100;
    border-image-outset: 20px 0px 0px 0px;
  }
  @media (max-width: $tablet-width) {
    border-top: 1px solid transparent;
    border-image: url("../img/top-border-2.png") round;
    border-image-width: 18px;
    border-image-slice: 100;
    border-image-outset: 18px 0px 0px 0px;
  }
}
.catalog__container {
  position: relative;
  z-index: 0;

}
.catalog__number {
  position: absolute;
  top: 114px;
  left: 0;
  z-index: -1;
}
.catalog__title {
  @include title-black();
  /*margin-bottom: 70px;*/
  @media (max-width: $desktop-width) {
    margin-bottom: 15px;
  }
}
.card__title {
  margin: 0;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: normal;
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: none;
}
.card__arrow-img {
  max-width: 20px;
}
.card__text-wrapper {
  margin-top: 30px;
}
.card__cost {
  margin: 0;
  margin-bottom: 35px;
  font-size: 25px;
  font-weight: bold;
}
.card__text-content {
  margin: 0;
  margin-bottom: 14px;
}
.catalog__cards-wrapper {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $desktop-width) {
    display: block;
  }
}
.catalog__text-card,
.catalog__card {
  width: 520px;
  padding: 0 45px;
  @media (max-width: $desktop-width) {

    width: auto;
    padding: 0;
  }
}
.catalog__text-card {
  @media (max-width: $desktop-width) {
    max-width: 550px;
    margin-bottom: 40px;
  }
}
.catalog__card {
  z-index: 1;
  position: relative;
  margin-bottom: 150px;
  @media (max-width: $desktop-width) {
    margin-bottom: 65px;
    max-width: 440px;
    margin-left: auto;
    margin-right: auto;
  }

