  //   height: 67px;
  //   background-image: url("../img/arrow-left.svg");
  //   background-repeat: no-repeat;
  // }
}
.card__arrow--left {
  z-index: 2;
  position: absolute;
  top: 210px;
  left: 30px;
  content: "";
  width: 30px;
  height: 45px;
  padding-left: 5px;
  padding-right: 5px;
}
.card__arrow--right {
  z-index: 2;
  position: absolute;
  top: 210px;
  right: 30px;
  content: "";
  width: 30px;
  height: 45px;
  padding-left: 5px;
  padding-right: 5px;
}
.text-card__title {
  font-size: 40px;
  line-height: 64px;
  @media (max-width: $desktop-width) {
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    margin-bottom: 20px;
  }
}
.card-btn {
  width: 110px;
  padding: 10px 15px;
  text-decoration: none;
}
.card__img {
  display: block;
  max-width: 385px;
  margin-left: auto;
  margin-right: auto;
  @media (max-width: $desktop-width) {
    max-width: 60%;
  }
  @media (max-width: $tablet-width) {
    max-width: 100%;
    margin-bottom: 20px;
  }
}
.owl-item  {
  background: none;
}
