.modal-wrapper {
  display: none;
  position: fixed; 
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,.7);
  z-index: 100;
}
.modal--show {
  display: flex;
}
.modal {
  position: relative;
  width: 55%;
  min-height: 200px;
  padding: 60px 105px 60px 105px;
  background-color: #ffffff;
  border-radius: 5px;
  @media (max-width: $desktop-width) {
    padding: 75px;
    width: 70%;
  }
  @media (max-width: $tablet-width) {
    padding: 45px;
     width: 85%;
  }

}
.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.modal__close-btn-wrapper {
    position: absolute;
    right: 38px;
    top: 38px;
    z-index: 0;
  &::after {
    position: absolute;
    content: "";
    width: 20px;
    height: 2px;
    background-color: #000000;
    transform: rotate(45deg);
    top: 14px;
    right: 0;
  }
  &::before {
    position: absolute;
    content: "";
    width: 20px;
    height: 2px;
    background-color: #000000;
    transform: rotate(-45deg);
    top: 14px;
    right: 0;
  }
}
.modal__close-btn-wrapper:hover {
  cursor: pointer;
}
.modal__close-btn {
  // position: absolute;
  // right: 40px;
  // top: 40px;
  display: block;
  width: 30px;
  height: 30px;
  opacity: 0;

}

.form__fields--modal {
 

  @media (min-width: $tablet-width) {
    width: 100%;
    padding: 20px;
    padding-right: 0;
    padding-left: 0;
  }
}
.fields__item--modal {
  @media (min-width: $desktop-width) {
    width: 45%;
  }
  @media (max-width: $desktop-width) {
    width: 100%;
  }
}
.fields__select-wrapper {
  width: 100%;
  margin-bottom: 60px;
}
.fields__select {
  width: 45%;
  border: none;
  border-bottom: 1px solid #dcddde;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  border-left: 1px solid #ffffff;
  min-height: 37px;

  @media (max-width: 1024px) {
    width: 100%;
  }
}


